@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Dancing Script', cursive;;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 96px;
  color: #D35563;
}

h3 {
  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 101px;
  color: #5A819E;
}

h4 {
  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 52px;
}

h5 {
  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 57px;
  color: #5A819E;
}

p {
  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 43px;

  color: #1a1a1a;
}

.container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap-reverse;
}

.container-left {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  
}

.itemleft0 {
  height: 100px;
  max-width: 450px;

  padding-top: 70px;
  padding-bottom: 0px;
  padding-left: 90px;

  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: normal;
  text-align: center;
  font-size: 36px;
  line-height: 52px;
}

.itemleft1 {
  height: 50px;
  max-width: 380px;
  padding-left: 125px;
  margin: 0px;

  font-family: "Noto Serif JP";
  font-style: normal;
  font-weight: normal;
  text-align: center;
  font-size: 36px;
  line-height: 52px;
}

.itemleft2 {
  height: 220px;
  text-align:center;
  max-width: 650px;
  padding-top: 40px;
  padding-bottom: 0px;
  padding-left: 25px
}

.textbio {
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 35px;
  color: #5A819E;
}
.name {
  font-family: "Dancing Script";
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 96px;
  color: #D35563;
}

.button-resume{
  width: 250px;
  height: 70px;
  border: none;
  border-radius: 58px;
  margin-left: 200px;
  text-align:center;

  background: #5A819E;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 200;
  font-size: 30px;
  line-height: 43px;
  outline: none;
  color: #FFFFFF;
  transition-duration: 0.4s;
}
.button-resume:hover {
  background-color: hsl(206, 27%, 60%); /* Green */
  color: white;
}


.container-right {
  padding-right: 80px;
  padding-left: 80px;
  padding-top: 20px;
  margin: 40px;
}

.imgBruna {
  max-width: 400px;
}

.bt-icon{
  padding-left: 10px;

}
.text-op {
  padding-left: 30px;
  margin-bottom: 0px;
}

.Rtable {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3em 0;
  padding: 0;
  text-align: center;
}

.Rtable-cell {
  box-sizing: border-box;
  flex-grow: 1;
  width: 100%;
  padding: 0.8em 1.2em;
  overflow: hidden;
  list-style: none;

}

.columname {
  font-family: 'Noto Serif JP';
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 43px;
  color: #D35563;
}

.rowtext {
  font-family: 'Noto Sans JP';
  font-style: normal;
  font-size: 20px;
  line-height: 43px;
  color: #1a1a1a;
}

.Rtable--4cols>.Rtable-cell {
  width: 25%;
}
.container-bottom{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.itembottom {
    max-width: 800px;
  }

.img-comm{
    width: 200px;
}
.format-h3{
  padding-left: 50px;
}
.container-top {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.itemtop {
max-width: 800px;
}
.format-img {
  width: 350px;
}

.container-middle {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding-left: 30px;
  margin-bottom: 0px;
}
.itemmiddle{

}

.format-h5{
  padding-left: 30px;
}
.container-project {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.format-desc {
    margin-bottom: 70px;
    max-width: 600px;
}

.format-img {
    width: 350px;
}

.format-title {
    margin-top: 0px;
    margin-bottom: 0px;
    text-align: center;
}
.title-proj{
  padding-left: 30px;
  margin-bottom: 0px;
}
@media screen and (min-width: 30em) {
  .contact-flex {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    text-align: center;
  }

  .containertop {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .contact-flex p {
    width: 600px;
    padding-bottom: 30px;
    color: #D35563;
  }
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #D35563;
}

.icon {
  font-size: 45px;
  color: black;
  padding-right: 10px;
  padding-bottom: 5px;
}
.linkslist{
  display: flex;
  flex-direction: column;
    text-align: center;
}
.linkslist li{
  text-decoration: none;
 
}
.centertext{
  align-self: baseline;
}
.navbar {
  display: flex;
  flex-wrap: wrap;
  background: hsl(206, 27%, 49%);
}

.leftside {
  display: flex;
  align-items: center;
}

.img-logo {
  height: 45px;
  width: 45px;
  padding-left: 20px;
}

.rightside {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
}

.rightside li a {
  text-decoration: none;
  color: white;
  padding: 1em 1em 0.8em;
  font-weight: 400;
  font-size: 20px;
}

.rightside li a:hover {
	background: hsl(206, 27%, 70%);
}

